.reg_Section, .prot{
    position: relative;
    width: fit-content;
    margin: 30% auto auto auto;
    border-radius: 20px;
    border: #9b4f11 solid;
    height: auto;
    background: #555555;
    padding: 20px;
    text-align: center;
}
.dis{
    display: none;
}
.but-prot{
    margin-top: 10px;
    display: flex;
    flex-flow: column;
    justify-content: space-evenly;
}

.inpu-prot{
    margin-bottom: 5px;
    width: 80%;
}

.prot-text{
     border-bottom: #5c1a0e solid;
 }
.but-text{
    border-bottom: #5c1a0e solid;
}
#ent-but{
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    font-size: 19px;
    background: #9b4f11 ;
    border: #9b4f11;
    width: 100%;
    border-radius: 20px;
}
#a{
    text-decoration: none;
    color: black;
    cursor: default
}
.text-regi{
    border-bottom: #5c1a0e solid;
}